import { takeEvery, call, put, cancel, all } from 'redux-saga/effects';
import API from '../api';
import * as actions from '../actions';

/*
  1. The weather service requires us to make a search by lat/lng to find its
  weather ID.
  2. We then use that weather ID to get the weather.

  This process is pretty well defined here with a saga.

  call invokes a method
  put dispatches an action
  takeEvery watches actions and executes a function

  Also -- the `*` in function is important; turns it into a "generator"

*/

const determineDroneLatAndLong = async () => {
  const response = await fetch(
    'https://react-assessment-api.herokuapp.com/api/drone'
  );
  if (!response.ok) {
    return { error: { code: response.status } };
  }
  const json = await response.json();
  let l = json.data.length - 1;
  return {
    metric: json.data[l].metric,
    latitude: json.data[l].latitude,
    longitude: json.data[l].longitude,
    metricsData: json.data
  };
};

function* watchWeatherIdReceived(action) {
  const { id } = action.payload;
  const { error, data } = yield call(API.findWeatherbyId, id);
  if (error) {
    yield put({ type: actions.API_ERROR, code: error.code });
    yield cancel();
    return;
  }
  yield put({ type: actions.WEATHER_DATA_RECEIVED, data });
}

function* watchFetchWeather(action) {
  const { error1, latitude, longitude, metric, metricsData } = yield call(
    determineDroneLatAndLong
  );
  if (error1) {
    console.log({ error1 });
    yield put({ type: actions.API_ERROR, code: error1.code });
    yield cancel();
    return;
  }
  const { error, data } = yield call(
    API.findLocationByLatLng,
    latitude,
    longitude
  );
  if (error) {
    console.log({ error });
    yield put({ type: actions.API_ERROR, code: error.code });
    yield cancel();
    return;
  }
  const location = data[0] ? data[0].woeid : false;
  if (!location) {
    yield put({ type: actions.API_ERROR });
    yield cancel();
    return;
  }
  yield put({
    type: actions.WEATHER_ID_RECEIVED,
    payload: { id: location, longitude, latitude, metric, metricsData }
  });
}

function* watchAppLoad() {
  yield all([
    takeEvery(actions.FETCH_WEATHER, watchFetchWeather),
    takeEvery(actions.WEATHER_ID_RECEIVED, watchWeatherIdReceived)
  ]);
}

export default [watchAppLoad];
